const wsClass = function (uri, prot) {        
    const self = this;
    const ws = require("ws");
    this.socket = new ws(uri, prot);
    this.socket.onopen = function () {
        self.onopen();
    };
    this.socket.onerror = function (error) {
        self.onerror(error);
    };
    this.socket.onclose = function (ev) {
        self.onclose(ev);
    };
    this.socket.onmessage = function (message) {
        if (message.type === 'message') {
            self.onmessage({data:message.data});
        }
        return true;
    };
}
wsClass.prototype.send = function (data) {
    if(this.socket)
        this.socket.send(data);
}
wsClass.prototype.close = function () {
    if(this.socket)
        this.socket.close();
}
Photon.PhotonPeer.setWebSocketImpl(wsClass);
module.exports = Photon;